@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
*{padding:0;margin:0;border:0}*,*:before,*:after{box-sizing:border-box}:focus,:active{outline:none}a:focus,a:active{outline:none}nav,footer,header,aside{display:block}html,body{height:100%;width:100%;font-size:100%;line-height:1;font-size:14px;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%}input,button,textarea{font-family:inherit}input::-ms-clear{display:none}button{cursor:pointer}button::-moz-focus-inner{padding:0;border:0}a,a:visited{text-decoration:none}a:hover{text-decoration:none}ul li{list-style:none}img{vertical-align:top}h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight:400}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;box-sizing:border-box}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}#root{background-image:url(/airplanes-task/static/media/mainBackgroundBlure.d2a545ed.jpg);background-position:center;background-repeat:no-repeat;background-size:cover;min-height:100vh;min-width:100vw}

.App{height:100vh;display:grid;align-items:center;justify-items:center;position:relative}

.login{background-color:#fff;color:#424242;width:410px;height:338px;box-shadow:0px 4px 10px rgba(0,0,0,0.15);border-radius:20px;font-family:"Roboto", sans-serif;line-height:28px;display:grid;align-items:center;grid-template-rows:1fr 3fr}.login h2{font-weight:500;font-size:24px}.login__button{background:linear-gradient(104.34deg, #3c4cad -15.34%, #00c3ff 145.95%);box-shadow:0px 0px 2px rgba(0,0,0,0.15);border-radius:4px;width:116px;height:34px;font-size:16px;line-height:19px;color:#fff}.login__button:hover{box-shadow:0px 0px 4px #99a0a3;cursor:pointer}.login__button:active{background:linear-gradient(104.34deg, #5362be -15.34%, #4ad5ff 145.95%);border:1.5px solid rgba(29,138,216,0.3);cursor:pointer}.login__header{text-align:center;padding:30px 0 0 0}.loginForm{height:100%;display:grid;margin:0 40px;grid-template-rows:1fr 1fr 1fr}.loginForm p{font-weight:300;font-size:16px}.loginForm input{border:1px solid #c9cacc;box-sizing:border-box;border-radius:4px;width:100%;padding:0 15px;line-height:32px;font-size:15px;font-weight:400}.loginForm button{justify-self:end}

.formControl{position:relative}.formControl+p{position:absolute;color:red;font-size:12px;line-height:14px}.formControl.error{border:1px solid #eb1717;box-shadow:0px 0px 4px rgba(235,23,23,0.2)}.form__input_lable.error{color:red}

.flights{height:100%;width:100%;position:relative;display:grid;justify-items:center;align-items:center}.flights__body{width:680px;height:895px;background:#ffffff;box-shadow:0px 4px 18px rgba(0,0,0,0.25);border-radius:20px;display:grid;padding:0 40px;grid-template-rows:1fr 1.5fr 3fr 1fr 10fr 1fr}.flights__exit{position:absolute;right:30px;top:30px}.flights__exit span{color:#1157a7;padding:0 10px;font-size:20px;line-height:13px;z-index:10}.flights__exit img{height:18px}.body__header{grid-row:2;display:grid;grid-auto-flow:column;align-items:center;justify-content:space-between}.body__header .header__info{font-family:"Roboto";font-style:normal;font-weight:500;font-size:32px;line-height:37px;color:#424242}.body__header .header__info img{margin:11px 15px}.body__header .header__date input{color:#1157a7;font-family:"Roboto";font-style:normal;font-weight:500;font-size:25px;line-height:22px;width:195px}.body__header .header__date .header__kalendar{height:20px;width:18px;margin:0 10px}.body__slaiderImages{grid-row:3;overflow:hidden;cursor:move}.body__favoriteCounter{grid-row:4;color:#424242;font-family:"Roboto";font-weight:300;font-size:17px;line-height:22px;display:grid;align-items:center;padding-left:10px}.body__favoriteCounter .counter{color:#1157A7;font-family:"Roboto";font-weight:500;font-size:20px;margin:0 5px}.body__flightsItems{grid-row:5;overflow:scroll;overflow-x:hidden}::-webkit-scrollbar{width:2px}::-webkit-scrollbar-track{background:white}::-webkit-scrollbar-thumb{background:#1157a7;border-radius:8px;height:100px}

.slot{display:grid;grid-template-columns:20% 1fr 25%;height:100px;align-items:center}.slot .logo{display:grid;align-items:center;justify-items:center}.slot .logo__body{height:64px;width:64px;background-image:url(data:image/png;base64,),url(data:image/png;base64,);background-repeat:no-repeat;background-position:center}.slot .info__route{font-weight:300;font-size:17px;line-height:22px}.slot .info__route img{margin:7px}.slot .info__date{color:#878787;font-size:14px;line-height:22px}.slot .info__date img{margin:11px}.slot .info__company{color:#878787;font-size:14px;line-height:22px}.slot .price{display:grid;justify-items:end;grid-auto-flow:row;grid-row-gap:20px;row-gap:20px;padding-right:20px}.slot .price__favorite{cursor:pointer}.slot .price__value{color:#878787;font-weight:300;font-size:11px;line-height:22px}.slot .price__value span{font-family:"Roboto";padding-left:8px;font-weight:normal;font-size:17px;line-height:22px;color:#424242}

